/* Ultimate override for the sidebar hover effect */
.sidebar .MuiListItemButton-root,
.sidebar .MuiButtonBase-root {
  position: relative;
  z-index: 1;
}

/* Create a pseudo-element to handle hover effects instead of relying on the background-color property */
.sidebar .MuiListItemButton-root::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  opacity: 0;
  transition: opacity 0.3s;
  pointer-events: none;
}

/* Light theme hover effect */
html:not([data-theme='dark']) .sidebar .MuiListItemButton-root:hover::before {
  opacity: 1;
  background: linear-gradient(145deg, #e6e9f4, #f0f7ff);
}

/* Dark theme hover effect */
html[data-theme='dark'] .sidebar .MuiListItemButton-root:hover::before {
  opacity: 1;
  background: linear-gradient(145deg, #2a2a2a, #353535);
}

/* Completely reset any background color properties */
.sidebar .MuiListItemButton-root,
.sidebar .MuiButtonBase-root,
.sidebar .MuiListItemButton-root:hover,
.sidebar .MuiButtonBase-root:hover,
.sidebar .MuiListItemButton-root:focus,
.sidebar .MuiButtonBase-root:focus,
.sidebar .MuiListItemButton-root:active,
.sidebar .MuiButtonBase-root:active {
  background-color: transparent !important;
  background-image: none !important;
}

/* Ensure menu item backgrounds remain transparent when not hovered */
.sidebar .MuiListItemButton-root {
  background: transparent !important;
}

/* Disable Material UI ripple effects */
.MuiTouchRipple-root {
  display: none !important;
} 